import axios from 'axios';

const request = axios.create({
  baseURL: 'https://api.themoviedb.org/3',
  params: {
    api_key: '3d6c850fedd64a507e51cfb2335f305c',
    language: 'ko-KR'
  }
  // api주소가 너무 길기 때문에 이런 기능이 있다.
});

export default {
  namespaced: true,
  state: {
    popluarMovie: [],
    nowPlayingMovie: [],
    upCommingMovie: [],
    movieDetail: []
  },
  mutations: {
    SET_POPLUAR_MOVIE(state, data) {
      state.popluarMovie = data;
    },
    SET_NOWPLAYING_MOVIE(state, data) {
      state.nowPlayingMovie = data;
    },
    SET_UPCOMMING_MOVIE(state, data) {
      state.upCommingMovie = data;
    },
    SET_DETAIL_MOVIE(state, data) {
      state.movieDetail = data;
    }
  },
  actions: {
    //인기있는 영화정보
    async getPopularMovie({ commit }) {
      // 개선 후(request를 활용)
      const result = await request('/movie/popular', {
        params: {
          page: '4'
          // 페이지, 이걸 변수에 담아야 써먹을 수 있다.
        }
      });
      // 개선 전
      // const result = await axios.get(
      //   'https://api.themoviedb.org/3/movie/popular?api_key=3d6c850fedd64a507e51cfb2335f305c'
      // );

      //api 호출 성공
      if (result.status === 200) {
        commit('SET_POPLUAR_MOVIE', result.data.results);
      }
    },

    //현재 상영중인 영화 정보
    async getNowPlayingMovie({ commit }) {
      const result = await request('/movie/now_playing');
      // const result = await axios.get(
      //   'https://api.themoviedb.org/3/movie/now_playing?api_key=3d6c850fedd64a507e51cfb2335f305c'
      // );

      //api 호출 성공
      if (result.status === 200) {
        commit('SET_NOWPLAYING_MOVIE', result.data.results);
      }
    },

    //현재 상영중인 영화 정보
    async getUpCommingMovie({ commit }) {
      const result = await request('/movie/upcoming');
      // const result = await axios.get(
      //   'https://api.themoviedb.org/3/movie/upcoming?api_key=3d6c850fedd64a507e51cfb2335f305c'
      // );

      //api 호출 성공
      if (result.status === 200) {
        commit('SET_UPCOMMING_MOVIE', result.data.results);
      }
    },

    async getDetailMovie({ commit }, movieId) {
      const result = await request(`/movie/${movieId}`, {
        params: {
          append_to_response: 'videos'
        }
      });
      console.log(result);
      // const result = await axios.get(
      //   `https://api.themoviedb.org/3/movie/${movieId}?api_key=3d6c850fedd64a507e51cfb2335f305c`
      // );

      //api 호출 성공
      if (result.status === 200) {
        commit('SET_DETAIL_MOVIE', result.data);
      }
    }
  }
};
