<template>
  <div>
    <MovieList title="Popluar" :movie="$store.state.movie.popluarMovie" />
    <MovieList
      title="Now Playing"
      :movie="$store.state.movie.nowPlayingMovie"
    />
    <MovieList title="UpComming" :movie="$store.state.movie.upCommingMovie" />
  </div>
</template>

<script>
// @ is an alias to /src
import MovieList from '@/components/MovieList.vue';

export default {
  name: 'Movie',
  components: {
    MovieList
  },
  mounted() {
    this.getInitMovie();
  },
  methods: {
    getInitMovie() {
      this.$store.dispatch('movie/getPopularMovie');
      this.$store.dispatch('movie/getNowPlayingMovie');
      this.$store.dispatch('movie/getUpCommingMovie');
    }
  }
};
</script>
